import React, { useState, useEffect, useCallback } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import './Documents.css'; // ,  CSS  

const apiUrl = process.env.REACT_APP_API_URL;

//      (  User.js/DocumentAnalysis.js)
const formatDate = (dateStr) => {
    if (!dateStr) return ' ';
    try {
        const date = new Date(dateStr);
        if (isNaN(date.getTime())) return ' ';
        //  DD.MM.YYYY
        return date.toLocaleDateString('ru-RU');
    } catch (e) {
        console.error("  :", dateStr, e);
        return ' ';
    }
};

function Documents() {
    const [documents, setDocuments] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState('');
    const navigate = useNavigate();
    //  useState  user,      ,  localStorage 
    const [user, setUser] = useState(null);

    //      localStorage
    useEffect(() => {
        const storedUser = localStorage.getItem('user');
        if (storedUser) {
            try {
                setUser(JSON.parse(storedUser));
            } catch (e) {
                console.error("    localStorage:", e);
                localStorage.removeItem('user');
                setError("  . ,  .");
                setLoading(false); //    
            }
        } else {
             setError("  .");
             setLoading(false); //    
             //   navigate('/login');
        }
    }, []); //     


    //   
    const fetchDocuments = useCallback(async () => {
        // ,  user    workerId
        if (!user || user.workerId === undefined) {
            console.log('   workerId    .');
             //  ,       ( user  null)
            if (user !== null) {
                 setError('   ID    .');
            }
            setLoading(false); //  
            return; // ,   workerId
        }

        setLoading(true); //   
        setError(''); //  

        try {
            console.log('  :', { userId: user.workerId, role: user.role }); // : user.id -> user.workerId
            const response = await axios.get(`${apiUrl}/api/documents`, {
                params: {
                    userId: user.workerId, // : user.id -> user.workerId
                    role: user.role
                }
            });
            console.log(' :', response.data);

            // ,   
             if (!Array.isArray(response.data)) {
                console.error("API /api/documents   :", response.data);
                throw new Error("    .");
            }

            //     ()
            response.data.forEach((doc, index) => {
                console.log(` ${index + 1} IDs:`, {
                    regnumber: doc.regnumber,
                    formseries: doc.formseries,
                    formnumber: doc.formnumber
                });
            });

            setDocuments(response.data);

        } catch (err) {
            console.error('   :', err.response || err);
            setError(err.response?.data?.error || '   ');
             setDocuments([]); //    
        } finally {
            setLoading(false); //     
        }
        //  useCallback  user.workerId  user.role
    }, [user, apiUrl]); //     user  apiUrl

    //    fetchDocuments   user  fetchDocuments
    useEffect(() => {
        //  fetchDocuments   ,  user     localStorage
        if (user) {
            fetchDocuments();
        }
    }, [user, fetchDocuments]); //   user  fetchDocuments


    //  
    const handleLogout = () => {
        localStorage.removeItem('user');
        setUser(null); //   
        navigate('/login');
    };

    const handleBack = () => {
         //  user   role
        if (user) {
            navigate(user.role === 1 ? '/admin' : '/user');
        } else {
            navigate('/login'); //   ,  user  
        }
    };

    // ---  ---

    //    ( user  null)
    if (user === null && loading) {
         return <div className="loading">  ...</div>;
    }

     //   ( user      )
     if (error) {
         return (
            <div className="documents-container">
                 <div className="documents-header">
                     <h1></h1>
                     <div className="header-buttons">
                          {/*        */}
                         <button onClick={handleBack} className="back-button"></button>
                         <button onClick={handleLogout} className="logout-button"></button>
                     </div>
                 </div>
                 <div className="error">{error}</div>
            </div>
         );
     }

    //    (user  )
    if (loading) {
        return <div className="loading"> ...</div>;
    }

     //   ,  user   null   (  ,   )
     if (!user) {
         return <div className="error">    . <button onClick={() => navigate('/login')}></button></div>;
     }


    //    
    return (
        <div className="documents-container">
            <div className="documents-header">
                <h1>{user.role === 1 ? ' ' : ' '}</h1>
                <div className="header-buttons">
                    <button onClick={handleBack} className="back-button"></button>
                    <button onClick={handleLogout} className="logout-button"></button>
                </div>
            </div>

            <div className="documents-list">
                {documents.length === 0 ? (
                    <p className="no-documents">  </p>
                ) : (
                    <table className="documents-table">
                        <thead>
                            <tr>
                                {user.role === 1 && <th></th>}
                                <th></th>
                                <th> </th>
                                <th> </th>
                                <th> </th>
                                <th> </th>
                                <th> </th>
                                <th> </th>
                            </tr>
                        </thead>
                        <tbody>
                            {documents.map((doc) => (
                                <tr key={doc.ID_Document}>
                                    {/*      */}
                                    {user.role === 1 && <td>{doc.WorkerName || 'N/A'}</td>}
                                    {/*   */}
                                    <td>{doc.ProgramName || 'N/A'}</td>
                                    <td>{doc.regnumber || 'N/A'}</td>
                                    <td>{doc.formseries || 'N/A'}</td>
                                    <td>{doc.formnumber || 'N/A'}</td>
                                    {/*     */}
                                    <td>{formatDate(doc.DateIssue)}</td>
                                    <td>{formatDate(doc.DataStart)}</td>
                                    <td>{formatDate(doc.DataEnd)}</td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                )}
            </div>
        </div>
    );
}

export default Documents;