import React, { useState, useEffect, useMemo, useCallback } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import './AdminPanel.css';

const apiUrl = process.env.REACT_APP_API_URL; //      

// ---      ---
const formatDate = (dateStr, type = 'date') => {
    if (!dateStr) return ''; //     input[type=date]   
    try {
        const date = new Date(dateStr);
        if (isNaN(date.getTime())) return ''; //  

        if (type === 'datetime-local') {
             //  YYYY-MM-DDTHH:mm () -   input type="datetime-local"
             //    ,      /
             const offset = date.getTimezoneOffset();
             const adjustedDate = new Date(date.getTime() - (offset*60*1000));
             return adjustedDate.toISOString().slice(0, 16);
        } else if (type === 'date') {
             //  YYYY-MM-DD (  input type="date")
             const year = date.getFullYear();
             const month = (date.getMonth() + 1).toString().padStart(2, '0');
             const day = date.getDate().toString().padStart(2, '0');
             return `${year}-${month}-${day}`;
        } else {
             //  DD.MM.YYYY (   )
             return date.toLocaleDateString('ru-RU');
        }
    } catch (e) {
        console.error("  :", dateStr, e);
        return ''; //     
    }
};


// ---  AddItemForm ( ) ---
const AddItemForm = ({ type, onSave, onCancel, programTypes = [], allOrganizations = [] }) => {
    // ... ( AddItemForm  ) ...
    const [newItem, setNewItem] = useState({});
    useEffect(() => { setNewItem({}); }, [type]);
    const handleChange = (field, value) => { setNewItem(prev => ({ ...prev, [field]: value })); };
    const handleSubmit = (e) => { e.preventDefault(); onSave(newItem); setNewItem({}); };
    const handleCancel = (e) => { e.preventDefault(); setNewItem({}); onCancel(); };

    if (type === 'programs') {
        return ( <form onSubmit={handleSubmit} className="add-form"> {/* ...    ... */}
             <h4> </h4>
                <input type="text" placeholder=" " required value={newItem.Name_ProgDPO || ''} onChange={(e) => handleChange('Name_ProgDPO', e.target.value)} />
                <select required value={newItem.id_type || ''} onChange={(e) => handleChange('id_type', e.target.value)}> <option value="" disabled> </option> {programTypes.map(pt => <option key={pt.id_type} value={pt.id_type}>{pt.Name_Type}</option>)} </select>
                <select required value={newItem.ID_Org || ''} onChange={(e) => handleChange('ID_Org', e.target.value)}> <option value="" disabled> </option> {allOrganizations.map(org => <option key={org.ID_Org} value={org.ID_Org}>{org.FName}</option>)} </select>
                <div className="form-actions"> <button type="submit"></button> <button type="button" onClick={handleCancel}></button> </div> </form> );
    }
    if (type === 'organizations') {
       return ( <form onSubmit={handleSubmit} className="add-form"> {/* ...    ... */}
        <h4> </h4>
           <input type="text" placeholder=" " required value={newItem.FName || ''} onChange={(e) => handleChange('FName', e.target.value)} />
           <input type="text" placeholder=" " required value={newItem.SName || ''} onChange={(e) => handleChange('SName', e.target.value)} />
           <div className="form-actions"> <button type="submit"></button> <button type="button" onClick={handleCancel}></button> </div> </form> );
    }
     if (type === 'kks') {
        return ( <form onSubmit={handleSubmit} className="add-form"> {/* ...   KKS ... */}
        <h4> KKS</h4>
           <input type="text" placeholder=" KKS" required value={newItem.Name_KKS || ''} onChange={(e) => handleChange('Name_KKS', e.target.value)} />
           <input type="text" placeholder=" " value={newItem.SName_KKS || ''} onChange={(e) => handleChange('SName_KKS', e.target.value)} />
           <div className="form-actions"> <button type="submit"></button> <button type="button" onClick={handleCancel}></button> </div> </form> );
    }
    return null;
};

// ---  WorkerForm ( ) ---
const WorkerForm = ({ initialData = {}, onSave, onCancel, isEditing = false }) => {
    // ... ( WorkerForm  ) ...
    const [workerData, setWorkerData] = useState(initialData);
    const [newPassword, setNewPassword] = useState('');
    useEffect(() => { setWorkerData({ ...initialData, Role: initialData.Role !== undefined ? initialData.Role : '' }); setNewPassword(''); }, [initialData]);
    const handleChange = (field, value) => { setWorkerData(prev => ({ ...prev, [field]: value })); };
    const handlePasswordChange = (e) => { setNewPassword(e.target.value); };
    const handleSubmit = (e) => { e.preventDefault(); const dataToSend = { ...workerData }; if (newPassword) { dataToSend.Password = newPassword; } else if (!isEditing) { alert('    .'); return; } onSave(dataToSend); };
    const handleCancelClick = (e) => { e.preventDefault(); onCancel(); };
    return ( <form onSubmit={handleSubmit} className="add-form user-form"> <h4>{isEditing ? `  (ID: ${workerData.ID_Worker})` : ' '}</h4> <input type="text" placeholder=" " required value={workerData.FName_Worker || ''} onChange={(e) => handleChange('FName_Worker', e.target.value)} /> <input type="text" placeholder="" required value={workerData.Login || ''} onChange={(e) => handleChange('Login', e.target.value)} /> <input type="password" placeholder={isEditing ? '  ( ,   )' : ''} value={newPassword} onChange={handlePasswordChange} required={!isEditing}/> <select required value={workerData.Role} onChange={(e) => handleChange('Role', parseInt(e.target.value, 10))}> <option value="" disabled> </option> <option value={0}> </option> <option value={1}>  ()</option> </select> <div className="form-actions"> <button type="submit"></button> <button type="button" onClick={handleCancelClick}></button> </div> </form> );
};

// ---      ---
const DocumentForm = ({ initialData = {}, onSave, onCancel, isEditing = false, allWorkers = [], allPrograms = [] }) => {
    const [docData, setDocData] = useState({});

    useEffect(() => {
        //        
        //     YYYY-MM-DD  input type="date"
        setDocData({
            ID_Worker: initialData.ID_Worker || '',
            ID_ProgDPO: initialData.ID_ProgDPO || '',
            regnumber: initialData.regnumber || '',
            formseries: initialData.formseries || '',
            formnumber: initialData.formnumber || '',
            DateIssue: formatDate(initialData.DateIssue, 'date'), // 
            DataStart: formatDate(initialData.DataStart, 'date'), // 
            DataEnd: formatDate(initialData.DataEnd, 'date')      // 
        });
    }, [initialData]);

    const handleChange = (field, value) => {
        setDocData(prev => ({ ...prev, [field]: value }));
    };

    const handleSubmit = (e) => {
        e.preventDefault();
        //     ,  
        const dataToSend = { ...docData };
         //  ID  ( ),  
        if (isEditing && initialData.ID_Document) {
            dataToSend.ID_Document = initialData.ID_Document;
        }
        onSave(dataToSend); //   
    };

    const handleCancelClick = (e) => {
        e.preventDefault();
        onCancel(); //   
    };

    return (
        <form onSubmit={handleSubmit} className="add-form document-form">
            <h4>{isEditing ? `  (ID: ${initialData.ID_Document})` : ' '}</h4>

            {/*   */}
            <label htmlFor="doc-worker">:</label>
            <select id="doc-worker" required value={docData.ID_Worker} onChange={(e) => handleChange('ID_Worker', e.target.value ? parseInt(e.target.value, 10) : '')}>
                <option value="" disabled>--   --</option>
                {allWorkers.map(w => <option key={w.ID_Worker} value={w.ID_Worker}>{w.FName_Worker} (ID: {w.ID_Worker})</option>)}
            </select>

             {/*   */}
             <label htmlFor="doc-program"> :</label>
             <select id="doc-program" required value={docData.ID_ProgDPO} onChange={(e) => handleChange('ID_ProgDPO', e.target.value ? parseInt(e.target.value, 10) : '')}>
                 <option value="" disabled>--   --</option>
                 {allPrograms.map(p => <option key={p.ID_ProgDPO} value={p.ID_ProgDPO}>{p.Name_ProgDPO}</option>)}
             </select>

            {/*   */}
            <label htmlFor="doc-regnumber"> :</label>
            <input id="doc-regnumber" type="text" placeholder=". " value={docData.regnumber} onChange={(e) => handleChange('regnumber', e.target.value)} />

            <label htmlFor="doc-formseries"> :</label>
            <input id="doc-formseries" type="text" placeholder="" value={docData.formseries} onChange={(e) => handleChange('formseries', e.target.value)} />

            <label htmlFor="doc-formnumber"> :</label>
            <input id="doc-formnumber" type="text" placeholder="" value={docData.formnumber} onChange={(e) => handleChange('formnumber', e.target.value)} />

             {/*  */}
             <label htmlFor="doc-dateissue"> :</label>
             <input id="doc-dateissue" type="date" value={docData.DateIssue} onChange={(e) => handleChange('DateIssue', e.target.value)} />

             <label htmlFor="doc-datastart"> :</label>
             <input id="doc-datastart" type="date" value={docData.DataStart} onChange={(e) => handleChange('DataStart', e.target.value)} />

             <label htmlFor="doc-dataend"> :</label>
             <input id="doc-dataend" type="date" value={docData.DataEnd} onChange={(e) => handleChange('DataEnd', e.target.value)} />

            <div className="form-actions">
                <button type="submit"></button>
                <button type="button" onClick={handleCancelClick}></button>
            </div>
        </form>
    );
};


// ---   AdminPanel ---
function AdminPanel() {
    // 
    const [activeTab, setActiveTab] = useState('programs'); // 'programs', 'organizations', 'kks', 'workers', 'documents'
    const [programs, setPrograms] = useState([]);
    const [organizations, setOrganizations] = useState([]);
    const [kks, setKks] = useState([]);
    const [workers, setWorkers] = useState([]);
    const [documents, setDocuments] = useState([]); // <<<    
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState('');
    const [editingItem, setEditingItem] = useState(null);
    const [addingItemType, setAddingItemType] = useState(null);
    const [programTypes, setProgramTypes] = useState([]);
    const [programTypesData, setProgramTypesData] = useState([]);
    const [allOrganizations, setAllOrganizations] = useState([]);
    const [allWorkers, setAllWorkers] = useState([]); // <<<    
    const [allPrograms, setAllPrograms] = useState([]); // <<<    
    const [sortConfig, setSortConfig] = useState({ key: null, direction: 'ascending' });

    const navigate = useNavigate();
    // apiUrl   

    //   ( )
    const user = useMemo(() => { /* ... */
         const storedUser = localStorage.getItem('user');
         try {
             return storedUser ? JSON.parse(storedUser) : null;
         } catch (e) {
             console.error("Failed to parse user from localStorage", e);
             localStorage.removeItem('user');
             return null;
         }
     }, []);

    // ---  ---
    const requestSort = (key) => { /* ... */
        let direction = 'ascending';
        if (sortConfig.key === key && sortConfig.direction === 'ascending') {
            direction = 'descending';
        }
        setSortConfig({ key, direction });
     };
    const getIdKeyForTab = (tab) => {
        switch (tab) {
            case 'programs': return 'ID_ProgDPO';
            case 'organizations': return 'ID_Org';
            case 'kks': return 'ID_KKS';
            case 'workers': return 'ID_Worker';
            case 'documents': return 'ID_Document';
            default: return null;
        }
    };
    useEffect(() => { /* ...     ... */
        const idKey = getIdKeyForTab(activeTab);
        if (idKey) {
            setSortConfig({ key: idKey, direction: 'ascending' });
        } else {
            setSortConfig({ key: null, direction: 'ascending' });
        }
        setEditingItem(null);
        setAddingItemType(null);
    }, [activeTab]);

    // ---   ---
    const fetchData = useCallback(async (forceAux = false) => {
        if (!user || user.role !== 1) { /* ...   ... */
             console.warn("fetchData called without admin user, aborting.");
             setLoading(false);
             setError(" .   .");
             return;
         }
        setLoading(true);
        setError('');
        console.log(`Workspaceing data. Active tab: ${activeTab}`);

        //    (, , , )
        //  allPrograms  allWorkers
        if (forceAux || programTypes.length === 0 || allOrganizations.length === 0 || allWorkers.length === 0 || allPrograms.length === 0 ) {
            try {
                console.log("Fetching auxiliary data (types, orgs, workers, programs)...");
                const endpoints = [
                    `${apiUrl}/api/program-types`,
                    `${apiUrl}/api/programs/types`,
                    `${apiUrl}/api/organizations`,
                    `${apiUrl}/api/workers/admin`, //    
                    `${apiUrl}/api/programs`,       //    
                ];
                 //  Promise.all  4 
                const [typesRes, orgsRes, workersRes, progsRes] = await Promise.all(
                    endpoints.map(url => axios.get(url))
                );
                const fetchedTypes = typesRes.data || [];
                setProgramTypes(fetchedTypes);     //  dropdown  
                setProgramTypesData(fetchedTypes);

                setProgramTypes(typesRes.data || []);
                setAllOrganizations(orgsRes.data || []);
                setAllWorkers(workersRes.data || []); //   
                setAllPrograms(progsRes.data || []);   //   
                console.log("Auxiliary data fetched.");
            } catch (err) {
                console.error('Error fetching auxiliary data:', err);
             const auxErrorMsg = '    (, , , ).';
             setError(prev => prev ? `<span class="math-inline">\{prev\}\\n</span>{auxErrorMsg}` : auxErrorMsg);
              //       
              setProgramTypes([]);
              setProgramTypesData([]); // <<< 
              setAllOrganizations([]);
              setAllWorkers([]);
              setAllPrograms([]);
            }
        }

        //   
        try {
            let response;
            let endpoint;
            let config = {};
            switch (activeTab) {
                case 'programs': endpoint = `${apiUrl}/api/programs`; break;
                case 'organizations': endpoint = `${apiUrl}/api/organizations`; break;
                case 'kks': endpoint = `${apiUrl}/api/kks`; break;
                case 'workers': endpoint = `${apiUrl}/api/workers/admin`; break;
                case 'documents':
                    endpoint = `${apiUrl}/api/documents`;
                    // <<<      >>>
                    if (user?.workerId && user?.role !== undefined) {
                       config.params = {
                           userId: user.workerId, // ID  
                           role: user.role      //    (  1)
                       };
                    } else {
                        //     ,  (   )
                        console.error("Admin user data (workerId, role) is missing for documents fetch.");
                        setError("      .");
                        setLoading(false);
                        return;
                    }
                    break; 
                default:
                    console.warn(`Unknown activeTab: ${activeTab}`);
                    setLoading(false);
                    return;
            }
            console.log(`Attempting to fetch ${endpoint}...`, config); //  
            // <<<  config   >>>
            response = await axios.get(endpoint, config); 
            console.log(`Received response from ${endpoint}`, response.status);

            //      
            const responseData = Array.isArray(response.data) ? response.data : [];
            if (!Array.isArray(response.data)) {
                 console.warn(`API endpoint ${endpoint} did not return an array.`);
            }

            switch (activeTab) {
                case 'programs': setPrograms(responseData); break;
                case 'organizations': setOrganizations(responseData); setAllOrganizations(responseData); break;
                case 'kks': setKks(responseData); break;
                case 'workers': setWorkers(responseData); break;
                case 'documents': setDocuments(responseData); break;
            }
        } catch (err) {
            console.error(`Error fetching ${activeTab}:`, err.response || err.message || err);
            const mainErrorMsg = `     "${activeTab}".`;
            setError(prev => prev ? `${prev}\n${mainErrorMsg}` : mainErrorMsg);
             //     
             switch (activeTab) {
                 case 'programs': setPrograms([]); break;
                 case 'organizations': setOrganizations([]); break;
                 case 'kks': setKks([]); break;
                 case 'workers': setWorkers([]); break;
                 case 'documents': setDocuments([]); break;
             }
        } finally {
            console.log(`Setting loading to false for ${activeTab}.`);
            setLoading(false);
        }
    }, [activeTab, user, apiUrl, programTypes.length, allOrganizations.length, allWorkers.length, allPrograms.length]); //  

    useEffect(() => {
         if (!user || user.role !== 1) {
             navigate('/login');
             return;
         }
         fetchData(true);
     }, [user, navigate, fetchData]);

    // ---    ---
    const sortedPrograms = useMemo(() => { 
        let sortableItems = [...programs]; if (sortConfig.key) { sortableItems.sort((a, b) => { if (a[sortConfig.key] < b[sortConfig.key]) { return sortConfig.direction === 'ascending' ? -1 : 1; } if (a[sortConfig.key] > b[sortConfig.key]) { return sortConfig.direction === 'ascending' ? 1 : -1; } return 0; }); } return sortableItems;
     }, [programs, sortConfig]);
    const sortedOrganizations = useMemo(() => { 
        let sortableItems = [...organizations]; if (sortConfig.key) { sortableItems.sort((a, b) => { const valA = a[sortConfig.key]; const valB = b[sortConfig.key]; const compareResult = typeof valA === 'number' && typeof valB === 'number' ? valA - valB : String(valA).localeCompare(String(valB)); return sortConfig.direction === 'ascending' ? compareResult : -compareResult; }); } return sortableItems;
     }, [organizations, sortConfig]);
    const sortedKks = useMemo(() => { 
        let sortableItems = [...kks]; if (sortConfig.key) { sortableItems.sort((a, b) => { const valA = a[sortConfig.key]; const valB = b[sortConfig.key]; const compareResult = typeof valA === 'number' && typeof valB === 'number' ? valA - valB : String(valA).localeCompare(String(valB)); return sortConfig.direction === 'ascending' ? compareResult : -compareResult; }); } return sortableItems;
     }, [kks, sortConfig]);
    const sortedWorkers = useMemo(() => { 
        let sortableItems = [...workers]; if (sortConfig.key) { sortableItems.sort((a, b) => { const valA = a[sortConfig.key]; const valB = b[sortConfig.key]; if (sortConfig.key === 'Role' && typeof valA === 'number' && typeof valB === 'number') { return sortConfig.direction === 'ascending' ? valA - valB : valB - valA; } if (sortConfig.key === 'FName_Worker') { const nameA = String(valA ?? '').toLowerCase(); const nameB = String(valB ?? '').toLowerCase(); if (nameA < nameB) return sortConfig.direction === 'ascending' ? -1 : 1; if (nameA > nameB) return sortConfig.direction === 'ascending' ? 1 : -1; return 0; } const compareResult = typeof valA === 'number' && typeof valB === 'number' ? valA - valB : String(valA ?? '').localeCompare(String(valB ?? '')); return sortConfig.direction === 'ascending' ? compareResult : -compareResult; }); } return sortableItems;
    }, [workers, sortConfig]);
    const sortedDocuments = useMemo(() => {
        let sortableItems = [...documents];
        if (sortConfig.key) {
            sortableItems.sort((a, b) => {
                const valA = a[sortConfig.key];
                const valB = b[sortConfig.key];

                //   
                 if (['DateIssue', 'DataStart', 'DataEnd'].includes(sortConfig.key)) {
                    const dateA = valA ? new Date(valA).getTime() : 0;
                    const dateB = valB ? new Date(valB).getTime() : 0;
                     if (isNaN(dateA) && isNaN(dateB)) return 0;
                     if (isNaN(dateA)) return sortConfig.direction === 'ascending' ? -1 : 1; // nulls/invalid first or last
                     if (isNaN(dateB)) return sortConfig.direction === 'ascending' ? 1 : -1;
                    return sortConfig.direction === 'ascending' ? dateA - dateB : dateB - dateA;
                }

                 //      
                if (['WorkerName', 'ProgramName'].includes(sortConfig.key)) {
                     const strA = String(valA ?? '').toLowerCase();
                     const strB = String(valB ?? '').toLowerCase();
                     if (strA < strB) return sortConfig.direction === 'ascending' ? -1 : 1;
                     if (strA > strB) return sortConfig.direction === 'ascending' ? 1 : -1;
                     return 0;
                }

                //  
                const compareResult = typeof valA === 'number' && typeof valB === 'number'
                    ? valA - valB
                    : String(valA ?? '').localeCompare(String(valB ?? ''));

                return sortConfig.direction === 'ascending' ? compareResult : -compareResult;
            });
        }
        return sortableItems;
    }, [documents, sortConfig]);

    // ---  CRUD ---
    const handleEdit = (item) => { setEditingItem(item); setAddingItemType(null); };
    const handleCancel = () => { setEditingItem(null); };
    const handleChangeInline = (field, value) => { setEditingItem(prev => ({ ...prev, [field]: value })); };
    const handleSave = async (editedData) => {
        const itemToSave = editedData || editingItem;
        if (!itemToSave) return;
        const idKey = getIdKeyForTab(activeTab);
        const id = itemToSave[idKey];
        if (id === undefined && activeTab !== 'documents') { //   ID     itemToSave  
             if (activeTab === 'documents' && editingItem?.ID_Document) {
                 //  ID   editingItem  
                 id = editingItem.ID_Document;
                 itemToSave.ID_Document = id; //  ID   PUT ,  
             } else {
                console.error("Cannot save item without ID", itemToSave);
                setError("   ID  .");
                return;
             }
        }
        console.log(`Saving edited item (ID: ${id}, Tab: ${activeTab}):`, itemToSave);
        setLoading(true);
        setError('');
        try {
            let endpoint;
            switch (activeTab) {
                case 'programs': endpoint = `${apiUrl}/api/programs/${id}`; break;
                case 'organizations': endpoint = `${apiUrl}/api/organizations/${id}`; break;
                case 'kks': endpoint = `${apiUrl}/api/kks/${id}`; break;
                case 'workers': endpoint = `${apiUrl}/api/workers/${id}`; break;
                case 'documents': endpoint = `${apiUrl}/api/documents/${id}`; break; // <<<  ( PUT /api/documents/:id)
                default: throw new Error("   ");
            }
            await axios.put(endpoint, itemToSave);
            setEditingItem(null);
            await fetchData();
            console.log(`Item (ID: ${id}) saved successfully.`);
        } catch (err) {
            console.error('   :', err.response?.data || err.message || err);
            setError(err.response?.data?.error || '   ');
        } finally {
            setLoading(false);
        }
    };
    const handleShowAddForm = () => { setAddingItemType(activeTab); setEditingItem(null); };
    const handleCancelAdd = () => { setAddingItemType(null); };
    const handleAddItem = async (newItemData) => { //  (POST)
        const type = addingItemType;
        if (!type) return;
        console.log(`Adding new ${type}:`, newItemData);
        setLoading(true);
        setError('');
        try {
            let endpoint;
            switch (type) {
                case 'programs': endpoint = `${apiUrl}/api/programs`; break;
                case 'organizations': endpoint = `${apiUrl}/api/organizations`; break;
                case 'kks': endpoint = `${apiUrl}/api/kks`; break;
                case 'workers': endpoint = `${apiUrl}/api/workers`; break;
                case 'documents': endpoint = `${apiUrl}/api/documents`; break;
                default: throw new Error("   ");
            }
            await axios.post(endpoint, newItemData);
            setAddingItemType(null);
            await fetchData();
            console.log(`Item of type ${type} added successfully.`);
        } catch (err) {
            console.error(`   ${type}:`, err.response?.data || err.message || err);
            setError(err.response?.data?.error || `   `);
        } finally {
            setLoading(false);
        }
    };
    const handleDelete = async (idToDelete) => { //  (DELETE)
        if (!window.confirm(' ,     ?')) { return; }
        const idKey = getIdKeyForTab(activeTab);
        console.log(`Deleting item with ID: ${idToDelete} (Key: ${idKey}) on tab: ${activeTab}`);
        setLoading(true);
        setError('');
        try {
            let endpoint;
            switch (activeTab) {
                case 'programs': endpoint = `${apiUrl}/api/programs/${idToDelete}`; break;
                case 'organizations': endpoint = `${apiUrl}/api/organizations/${idToDelete}`; break;
                case 'kks': endpoint = `${apiUrl}/api/kks/${idToDelete}`; break;
                case 'workers': endpoint = `${apiUrl}/api/workers/${idToDelete}`; break;
                case 'documents': endpoint = `${apiUrl}/api/documents/${idToDelete}`; break; // <<<  ( DELETE /api/documents/:id)
                default: throw new Error("   ");
            }
            await axios.delete(endpoint);
            if (editingItem && editingItem[idKey] === idToDelete) { setEditingItem(null); }
            await fetchData();
            console.log(`Item (ID: ${idToDelete}) deleted successfully.`);
        } catch (err) {
            console.error(`    (ID: ${idToDelete}):`, err.response?.data || err.message || err);
            setError(err.response?.data?.error || '   . ,    .');
        } finally {
            setLoading(false);
        }
    };

    // ---    ---
    const displayRole = (roleValue) => { /* ... */ return roleValue === 1 ? ' ' : roleValue === 0 ? ' ' : ''; };
    const renderSortableHeader = (label, key) => ( /* ... */ <th onClick={() => requestSort(key)} className="sortable-header"> {label} {sortConfig.key === key ? (sortConfig.direction === 'ascending' ? ' ' : ' ') : ' '} </th> );

    // ---    ---
    if (!user && loading) return <div className="loading">    ...</div>;
    if (!user) return <div className="error">    . <button onClick={() => navigate('/login')}></button></div>;
    if (user.role !== 1) return <div className="error"> .</div>;
    const showLoadingIndicator = loading && !error;

    return (
        <div className="admin-panel">
            {/*  */}
            <div className="admin-header">
                <h1> </h1>
                <div className="header-buttons">
                   <button onClick={() => navigate('/admin')} className="back-button"></button>
                   <button onClick={() => { localStorage.removeItem('user'); navigate('/login'); }} className="logout-button"></button>
                </div>
            </div>

            {/*  */}
            <div className="admin-tabs">
                <button className={`tab-button ${activeTab === 'programs' ? 'active' : ''}`} onClick={() => setActiveTab('programs')}></button>
                <button className={`tab-button ${activeTab === 'organizations' ? 'active' : ''}`} onClick={() => setActiveTab('organizations')}></button>
                <button className={`tab-button ${activeTab === 'kks' ? 'active' : ''}`} onClick={() => setActiveTab('kks')}></button>
                <button className={`tab-button ${activeTab === 'workers' ? 'active' : ''}`} onClick={() => setActiveTab('workers')}></button>
                {/* <<<   >>> */}
                <button className={`tab-button ${activeTab === 'documents' ? 'active' : ''}`} onClick={() => setActiveTab('documents')}></button>
            </div>

            {/*     */}
            {showLoadingIndicator && <div className="loading-inline"> ...</div>}
            {error && <div className="error-inline">{error} <button onClick={() => setError('')} title=" "></button></div>}

            {/*   */}
            <div className="admin-content">

                 {/*  / */}
                 {(addingItemType === 'workers' || (editingItem && activeTab === 'workers')) && ( <WorkerForm key={editingItem ? editingItem.ID_Worker : 'add-worker'} initialData={editingItem || {}} onSave={editingItem ? handleSave : handleAddItem} onCancel={editingItem ? handleCancel : handleCancelAdd} isEditing={!!editingItem} /> )}
                 {addingItemType && addingItemType !== 'workers' && addingItemType !== 'documents' && ( <AddItemForm key={`add-${addingItemType}`} type={addingItemType} onSave={handleAddItem} onCancel={handleCancelAdd} programTypes={programTypes} allOrganizations={allOrganizations} /> )}
                 {/* <<<     >>> */}
                 {(addingItemType === 'documents' || (editingItem && activeTab === 'documents')) && (
                     <DocumentForm
                         key={editingItem ? editingItem.ID_Document : 'add-document'}
                         initialData={editingItem || {}}
                         onSave={editingItem ? handleSave : handleAddItem}
                         onCancel={editingItem ? handleCancel : handleCancelAdd}
                         isEditing={!!editingItem}
                         allWorkers={workers} //    ( allWorkers,   )
                         allPrograms={allPrograms} //   
                     />
                 )}


                {/*  "" */}
                {!editingItem && !addingItemType && (
                    <button onClick={handleShowAddForm} className="add-button">
                         {
                            activeTab === 'programs' ? '' :
                            activeTab === 'organizations' ? '' :
                            activeTab === 'kks' ? 'KKS' :
                            activeTab === 'workers' ? '' :
                            activeTab === 'documents' ? '' : '' // <<< 
                        }
                    </button>
                )}

                {/*   */}
                {activeTab === 'programs' && ( <table className="admin-table"> {/* ...   ... */} <thead><tr> {renderSortableHeader('', 'ID_ProgDPO')} {renderSortableHeader('', 'Name_ProgDPO')} {renderSortableHeader('', 'Name_Type')} <th></th> </tr></thead> <tbody> {sortedPrograms.map((program, index) => ( <tr key={program.ID_ProgDPO}> {editingItem?.ID_ProgDPO === program.ID_ProgDPO ? (<> <td>{index + 1}</td> <td> <input type="text" value={editingItem.Name_ProgDPO} onChange={(e) => handleChangeInline('Name_ProgDPO', e.target.value)} /> </td> <td> {program.Name_Type || 'N/A'} </td> <td> <button onClick={() => handleSave()}></button> <button onClick={handleCancel}></button> </td> </>) : (<> <td>{index + 1}</td> <td>{program.Name_ProgDPO}</td> <td>{program.Name_Type || 'N/A'}</td> <td className="actions-cell"> <button onClick={() => handleEdit(program)} disabled={!!addingItemType || !!editingItem}>.</button> <button onClick={() => handleDelete(program.ID_ProgDPO)} className="delete-button" disabled={!!addingItemType || !!editingItem}></button> </td> </> )} </tr> ))} </tbody> </table> )}
                 {/*   */}
                 {activeTab === 'organizations' && ( <table className="admin-table"> {/* ...   ... */} <thead><tr> {renderSortableHeader('', 'ID_Org')} {renderSortableHeader(' ', 'FName')} {renderSortableHeader(' ', 'SName')} <th></th> </tr></thead> <tbody> {sortedOrganizations.map((org, index) => ( <tr key={org.ID_Org}> {editingItem?.ID_Org === org.ID_Org ? (<> <td>{index + 1}</td> <td><input type="text" value={editingItem.FName} onChange={(e) => handleChangeInline('FName', e.target.value)} /></td> <td><input type="text" value={editingItem.SName} onChange={(e) => handleChangeInline('SName', e.target.value)} /></td> <td> <button onClick={handleSave}></button> <button onClick={handleCancel}></button> </td> </>) : (<> <td>{index + 1}</td> <td>{org.FName}</td> <td>{org.SName}</td> <td className="actions-cell"> <button onClick={() => handleEdit(org)} disabled={!!addingItemType || !!editingItem}>.</button> <button onClick={() => handleDelete(org.ID_Org)} className="delete-button" disabled={!!addingItemType || !!editingItem}></button> </td> </> )} </tr> ))} </tbody> </table> )}
                 {/*  KKS */}
                 {activeTab === 'kks' && ( <table className="admin-table"> {/* ...   ... */} <thead><tr> {renderSortableHeader('', 'ID_KKS')} {renderSortableHeader('', 'Name_KKS')} {renderSortableHeader(' ', 'SName_KKS')} <th></th> </tr></thead> <tbody> {sortedKks.map((kksItem, index) => ( <tr key={kksItem.ID_KKS}> {editingItem?.ID_KKS === kksItem.ID_KKS ? (<> <td>{index + 1}</td> <td><input type="text" value={editingItem.Name_KKS} onChange={(e) => handleChangeInline('Name_KKS', e.target.value)} /></td> <td><input type="text" value={editingItem.SName_KKS} onChange={(e) => handleChangeInline('SName_KKS', e.target.value)} /></td> <td> <button onClick={handleSave}></button> <button onClick={handleCancel}></button> </td> </>) : (<> <td>{index + 1}</td> <td>{kksItem.Name_KKS}</td> <td>{kksItem.SName_KKS}</td> <td className="actions-cell"> <button onClick={() => handleEdit(kksItem)} disabled={!!addingItemType || !!editingItem}>.</button> <button onClick={() => handleDelete(kksItem.ID_KKS)} className="delete-button" disabled={!!addingItemType || !!editingItem}></button> </td> </> )} </tr> ))} </tbody> </table> )}
                 {/*   */}
                 {activeTab === 'workers' && ( <table className="admin-table"> {/* ...   ... */} <thead><tr> {renderSortableHeader('', 'ID_Worker')} {renderSortableHeader('', 'Login')} {renderSortableHeader(' ', 'FName_Worker')} {renderSortableHeader('', 'Role')} <th></th> </tr></thead> <tbody> {sortedWorkers.map((w, index) => ( <tr key={w.ID_Worker}> <td>{index + 1}</td> <td>{w.Login}</td> <td>{w.FName_Worker}</td> <td>{displayRole(w.Role)}</td> <td className="actions-cell"> <button onClick={() => handleEdit(w)} disabled={!!addingItemType || !!editingItem}>.</button> <button onClick={() => handleDelete(w.ID_Worker)} className="delete-button" disabled={!!addingItemType || !!editingItem}></button> </td> </tr> ))} </tbody> </table> )}

                {/* <<<     >>> */}
                {activeTab === 'documents' && (
                    <table className="admin-table">
                        <thead>
                            <tr>
                                {renderSortableHeader('', 'ID_Document')}
                                {renderSortableHeader('', 'WorkerName')}
                                {renderSortableHeader('', 'ProgramName')}
                                {renderSortableHeader('. ', 'regnumber')}
                                {/*    : ,   */}
                                {renderSortableHeader(' ', 'DateIssue')}
                                <th></th>
                            </tr>
                        </thead>
                        <tbody>
                            {sortedDocuments.map((doc, index) => (
                                <tr key={doc.ID_Document}>
                                    {/*    ,  inline */}
                                    <td>{index + 1}</td>
                                    <td>{doc.WorkerName || 'N/A'}</td>
                                    <td>{doc.ProgramName || 'N/A'}</td>
                                    <td>{doc.regnumber || 'N/A'}</td>
                                    <td>{formatDate(doc.DateIssue, 'display')}</td> {/*  formatDate   */}
                                    <td className="actions-cell">
                                        {/*    DocumentForm */}
                                        <button onClick={() => handleEdit(doc)} disabled={!!addingItemType || !!editingItem}>.</button>
                                        {/*   */}
                                        <button onClick={() => handleDelete(doc.ID_Document)} className="delete-button" disabled={!!addingItemType || !!editingItem}></button>
                                    </td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                 )}

            </div> {/* admin-content */}
        </div> // admin-panel
    );
}

export default AdminPanel;